
const initialState = [
    {
        id:Math.random(),
        title: 'A_Simple_Favor',
        image: 'http://fr.web.img4.acsta.net/c_215_290/pictures/18/08/24/20/41/4399259.jpg',
        rating: 4,
    },
    {id:Math.random(),
        title: 'ASingleShotPoster',
        image: 'https://media.senscritique.com/media/000005438127/source_big/A_Single_Shot.png',
        rating: 2},
    {id:Math.random(),
        title: 'bladerunner',
        image: 'data:image/jpeg;base64,',
        rating: 5},
    {id:Math.random(),
        title: 'inception-film',
        image: 'https://is2-ssl.mzstatic.com/image/thumb/Video20/v4/fe/dc/f0/fedcf039-7cc9-89b1-b992-00b0d47a229e/pr_source.lsr/268x0w.png',
        rating: 1}
    
]

const movieListReducer =(state=initialState, action)=>{
    if (action.type==='ADDMOVIES'){
        return state.concat(action.movie)
    }
    if (action.type==='EDITMOVIES'){
        return state.map(el=>{
            if(el.id===action.id){
                return action.movie
            }
            return state
        })


        
    }
    if (action.type==='DELETEMOVIES'){
        return state.filter((el,index)=>index!=action.id)
    

     
        
    }
        return state
}
export default movieListReducer;
